import { CreateArticle } from '@/api/api'
export default {
  middleware: 'authenticated', // 也可以是[]
  name: 'EditerIndex',
  data(){
    return {
      article: {
        title: '',
        description: '',
        body: '',
        tagList: []
      }
    }
  },
  methods: {
    async CreateArticle() {
      const { data } = await CreateArticle({article: this.article})
      console.log(data)
      if (data.article){
        // this.$router.push({
        //   })
          alert('创建成功！')
          this.article =  {
        title: '',
        description: '',
        body: '',
        tagList: []
      }
      }
    },
  },
}
